<template>
  <v-container>
    <v-form ref="loginForm">
      <v-text-field
        prepend-icon="smartphone"
        clearable
        name="login"
        :rules="[v => !!v || '手机号不能为空']"
        label="手机号"
        type="text"
        v-model="loginInfo.mobileNum"
      >
      </v-text-field>
      <v-text-field
        prepend-icon="lock"
        :append-icon="pwdShow ? 'visibility' : 'visibility_off'"
        clearable
        name="password"
        :rules="[v => !!v || '密码不能为空']"
        label="密码"
        :type="pwdShow ? 'text' : 'password'"
        v-model="loginInfo.userPwd"
        @click:append="pwdShow = !pwdShow"
      >
      </v-text-field>
      <v-btn
        block
        color="primary"
        large
        dark
        @click="submit"
      >登录</v-btn>
    </v-form>
  </v-container>
</template>

<script>
import { mapActions } from "vuex";
import { VNode } from 'vue'
export default {
  name: "Login",
  data() {
    return {
      pwdShow: false,
      loginInfo: {
        mobileNum: "",
        userPwd: ""
      },
      rules: {}
    };
  },
  methods: {
    submit() {
      if (this.$refs.loginForm.validate()) {
          this.login(this.loginInfo)
          // this.$router.push('roomState');
      }
    },
    ...mapActions({
      msg: "msg",
      login: 'login'
    })
  },
};
</script>

